.recipe{
	border: solid;
	border-radius: 20px;
	box-shadow: 0px 5px 20px rgb(71, 71, 71);
	margin: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	background: white;
	align-items: center;
	min-width: 40%;
}

.image{
	border: solid;
	border-width: 5px;
	border-radius: 50%;
	width: 250px;
	height: 250px;
}